/** @format */

import React from "react";
import classes from "./ProgressHeader.module.css";
import styled from "styled-components";

const Header = styled.header`
  background-color: #f9f9fb;
  font-family: Roboto, "Sans Serif";
  position: relative;

  &:before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: ${(props) => (props.progress ? props.progress + "%" : "20%")};
    height: 2px;
    background-color: #afbbcb;
  }
`;
const ProgressHeader = (props) => {
  return (
    <Header progress={props.progress} className={classes.ProgressHeader}>
      <nav>
        <ul className={classes.Header_ul}>
          <li>Add Business</li>
          <li>Link Social accounts</li>
          <li>Done</li>
        </ul>
      </nav>
    </Header>
  );
};

export default ProgressHeader;
